import React, { Component } from 'react'

export default class Panel extends Component {
    render() {
        return (
            <div>
                <div className="panel-nav">
                    <h2>CONTROL PANEL</h2>
                </div>
                <div className="panel-grid">
                    <div className="panel-section">
                        <div className="panel-nav panel-section-border letter-spacing">
                            MENU
                        </div>
                        <div className="panel-section-border button" onClick={() => {
                            this.props.history.push("cpanel/categories")
                        }}>
                            Categories
                        </div>
                        <div className="panel-section-border button" onClick={() => {
                            this.props.history.push("cpanel/items")
                        }}>
                            Items
                        </div>
                    </div>
                    <div className="panel-section">
                        <div className="panel-nav panel-section-border letter-spacing">
                            USER
                        </div>
                        <div className="panel-section-border">
                            Share link
                        </div>
                        <div className="panel-section-border">
                            Disable ads
                        </div>
                        <div className="panel-section-border">
                            Log out
                        </div>
                    </div>
                    <div className="panel-section">
                        <div className="panel-nav panel-section-border letter-spacing">
                            SETTINGS
                        </div>

                        <div className="panel-section-border">
                            Change display name
                        </div>
                        <div className="panel-section-border">
                            Change web address
                        </div>
                        <div className="panel-section-border">
                            Change color palette
                        </div>
                        <div className="panel-section-border">
                            Change brand logo
                        </div>
                    </div>
                </div>
                <div className="as-footer">
                    <div className=" panel-section-border button" onClick={() => {
                        this.props.history.push("/menu/" + "rimuru")
                    }}>
                        Go to your menu
                    </div>
                </div>
            </div>
        )
    }
}
